.btn-danger {
    @include immaterial-button((
        // Primary flat button.
        base-color: $danger-button-color,
        opacity: 0.12,
    ));

    &-flat {
        @include immaterial-button((
            // Primary flat button.
            base-color: $danger-button-color,
            opacity: 0.2,
            is-flat: 'true',
        ));
    }
}
